/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  //   box-shadow: inset 0 0 5px grey;
  //   border-radius: 10px;
  background: $bg-color;
  transition: all 1s;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: darken($bg-color, 15);
}
