@media (max-width: 1200px) {
  html {
    font-size: 60% !important;
  }
}
@media (max-width: 1110px) {
  #hero {
    padding-top: 15vh !important ;
  }
  .main {
    padding-left: 0 !important;
    padding: 0 1rem !important;
  }
  .nav {
    flex-direction: row;
    width: 100%;
    height: min(10vh, 10rem) !important;
    clip-path: inset(0 0 0 0);
    background-color: darken($bg-color, 20%) !important;
    &__hamburger {
      opacity: 0;
      pointer-events: none;
    }
    &__copyright {
      display: none;
    }
  }
}
@media (max-width: 1032px) {
  html {
    font-size: 50% !important;
  }
  .main {
    padding-left: 0 !important;
    padding: 0 4rem !important;
  }
  .daily {
    width: 100% !important;
    &__card {
      flex: 8rem 1 1;
      margin-bottom: 4rem !important;
      margin-right: 2rem !important;
    }
  }
  .detail {
    &__daily {
      width: 100% !important;
    }
  }
  .search__form {
    width: 70% !important;
  }
}
@media (max-width: 875px) {
  html {
    font-size: 40% !important;
  }
  .search__form {
    width: 80% !important;
  }
  .nav {
    &__link {
      margin: 0 1rem !important;
    }
  }
}
@media (max-width: 700px) {
  html {
    font-size: 45% !important;
  }

  .daily {
    // width: 100% !important;
    &__card {
      flex: 30% 1 1;
      // margin-bottom: 4rem !important;
      // margin-right: 2rem !important;
    }
  }
  .detail {
    &__flex {
      flex-wrap: wrap;
    }

    &__general {
      display: flex;
      flex-direction: row !important;
      justify-content: space-between !important;
    }

    &__daily {
      width: 100% !important;
      &--prediction {
        flex: 50% 1 1 !important;
      }
    }
    &__sun,
    &__moon {
      flex: 25% 1 1 !important;
    }
  }
  .flex {
    width: 100% !important;
    align-self: stretch;
    margin-top: 8rem;
  }
  .precitipation,
  .uvIndex {
    margin-right: 4rem !important;
  }
  .search__form {
    flex-wrap: wrap;
    border-radius: 0;
    align-items: center;
    &--input {
      flex: 100% 1 1 !important;
      border-radius: 1000px;
      height: 8rem;
      font-size: 3rem;
    }
    &--btn {
      width: 50%;
      height: 8rem;
      margin: 0 auto;
      margin-top: 5rem;
      border-radius: 1000px;
      font-size: 4rem;
    }
  }
}
@media (max-width: 500px) {
  html {
    font-size: 50% !important;
  }
  .nav {
    top: 100% !important;
    transform: translateY(-100%);
    justify-content: space-between !important;
    &__hamburger {
      display: none;
    }
    button {
      text-align: center !important;
    }
    h2 {
      display: none;
    }
  }
  .owner_info {
    padding: 0 !important;
    padding-bottom: 20vh !important;
  }
  .flex {
    flex-wrap: wrap;
  }
  .detail__general {
    margin: 0 !important;
    width: 100%;
    align-items: center !important;
  }
  .precitipation,
  .humidity,
  .uvIndex,
  .wind {
    margin: 0 !important;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 430px) {
  html {
    font-size: 50% !important;
    text-align: center;
  }
  .hero__extra {
    display: none;
  }

  .detail__daily--prediction {
    flex: 100% 1 1 !important;
    text-align: center;
    margin-bottom: 8rem !important;
    p {
      line-height: 3rem !important;
      color: white !important;
      // opacity: 1 !important;
      font-size: 3rem !important;
    }
  }
}
