.daily {
  width: 1024px;
  margin: 0 auto;
  color: white;

  .daily__flex {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 4rem 0;
  }
  &__heading {
    font-size: 4.8rem;
    color: $link-color;
  }
  &__card {
    padding: 3rem 2.5rem;
    margin: 4rem 0;
    background-color: $card-color;
    //     display: inline-block;
    border-radius: 01rem;
    //     flex: 30rem 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child) {
      margin-right: 2rem;
    }

    &--date {
      font-family: $font-bold !important;
      font-size: 3.6rem;
      margin-bottom: 1rem;
    }
    &--sign {
      height: 4rem;
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
    &--temp {
      font-size: 3.6rem;
      font-family: $font-thin;
      sup {
        vertical-align: top;
        font-size: 1.5rem;
      }
      grey {
        color: darken($text-color-white, 20);
      }
      span {
        font-size: 3rem;
        color: darken($text-color-white, 20);
        vertical-align: top;
      }
    }
    &--status {
      font-size: 2rem;
      text-align: center;
      text-transform: capitalize;
      margin-top: 1rem;
    }
  }
}
