@import "variables";
@import "helper";
@import "mixing";
@import "scrollbar";
@import "nav";
@import "search";
@import "layout";
@import "dailyCard";
@import "preloader";
@import "media";

@font-face {
  font-family: $font-light;
  src: url("../font/Phenomena-Light.woff") format("woff");
}
@font-face {
  font-family: $font-thin;
  src: url(../font/Phenomena-Thin.woff) format("woff");
}
@font-face {
  font-family: $font-bold;
  src: url(../font/Phenomena-Bold.woff) format("woff");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-thin;
  letter-spacing: 0.1rem;
}
i,
svg,
h2 {
  pointer-events: none;
}
html {
  font-size: 62.5%;
  transition: all 0.5s ease-in-out;
  scroll-behavior: smooth;
}
.nav {
  height: 100vh;
  //   width: $widthOfNav;
  background-color: $nav-color;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
body {
  overflow-x: hidden;
  background-color: $bg-color;
}
.main {
  //   width: calc(100vw-$widthOfNav);
  // min-height: 200vh;
  transition: all 0.3s ease-in-out;
  background-color: $bg-color;
  position: relative;
  padding-left: 6vw;
  z-index: 0;
  // backface-visibility: hidden;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    opacity: 0.4;
    transition: all 1s;
    filter: blur(0px);
    z-index: -1;
    pointer-events: none;
    transform: translateY(0px);
    transition: transform 0.1s;

    // backface-visibility: hidden;
  }
}
.owner_info {
  text-decoration: none;
  display: block;
  text-align: center;
  font-size: 2rem;
  color: white;
  padding: 4rem;
  letter-spacing: 1rem;
  opacity: 0.5;
  text-transform: uppercase;
}
