.search {
  width: 100%;
  height: 100vh;
  position: fixed;
  padding-right: 15px;
  // display: none;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s ease-in-out;
  &__close {
    position: fixed;
    top: 20%;
    right: 10%;
    font-size: 4rem;
    // padding: 1rem;
    background-color: transparent;
    outline: none;
    border: none;
    color: white;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
  &__form {
    @include center;
    overflow: hidden;
    width: 50%;
    display: flex;

    &--input {
      font-size: 2rem;
      padding: 1.5rem 4rem;
      outline: none;
      border: none;
      flex: 1;
      //       border-radius: 1000px 0 0 1000px;
      //         width: 90%;
      &::placeholder {
        padding: 0 2rem;
        letter-spacing: 0.5rem;
      }
    }
    &--btn {
      font-size: 1.6rem;
      outline: none;
      border: none;
      color: white;
      //       border-radius: 1000px 0 0 1000px;
      width: 9.9%;
      //         height: 100%;
      background-color: $nav-color;
      cursor: pointer;
    }
  }
}
