.preloader {
  background-color: $bg-color;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  //   background-color: red;
  z-index: 1000;
  display: flex;
  justify-content: center;
  img {
    transform: rotateZ(-50deg);
  }
  transition: all 0.5s ease-out;
  //   border-radius: 1000px;
}
.remove_preloader {
  opacity: 0;
  pointer-events: none;
}
