.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s cubic-bezier(0.61, 0.47, 0.25, 1.41) !important;
  // transform 1s ease-in -0.5s;
  clip-path: inset(0 75% 0 0);
  // transform: translateX(-100%);
  //   justify-content: space-evenly;
  &__hamburger {
    transition: all 0.2s ease-in-out;
    //     width: 100%;
    padding: 2rem !important;
    margin-bottom: 4rem;
    //     background-color: white;
    font-size: 3rem !important;
    color: $nav-icon-color;
    text-decoration: none;
    opacity: 1;
    margin-right: auto;
  }
  &:hover &__hamburger {
    opacity: 0;
    pointer-events: none;
  }
  &:hover {
    clip-path: inset(0 0% 0 0);
  }

  .nav__search,
  .nav__home,
  .nav__fav {
    background-color: transparent;
    text-align: left;
    // display: block;
    color: $nav-icon-color;
    width: 100%;
    text-decoration: none;
    font-size: 2.5rem;
    //     margin: 0.5rem;
    padding: 2rem;
    cursor: pointer;
    //     padding-bottom: 2rem;
    //     font-weight: 300;
    position: relative;
    border: none;
    outline: none;
    &::before {
      content: " ";
      width: 3px;
      height: 90%;
      background-color: white;
      position: absolute;
      top: 0;
      left: 0;
      transform: scaleY(0);
      transition: transform 0.1s ease-in-out;
      //       border-radius: 2px;
    }
    &:hover {
      color: $link-color;
    }
    &:hover::before {
      //       background-color: rgb(216, 17, 17);
      //       color: $nav-color;
      transform: scaleY(1);
    }
  }
  &__link {
    display: inline-block;
    font-size: 2.5rem;
    margin: 0 5rem;
    letter-spacing: 0.3rem;
  }
  &__copyright {
    display: block;
    margin-top: auto;
    align-self: end;
    text-decoration: none;
    color: whitesmoke;
    margin-bottom: 2rem;
    padding: 2rem;
    font-size: 1.6rem;
    opacity: 0;
    pointer-events: none;
    letter-spacing: 0.2rem;
  }
  &:hover &__copyright {
    opacity: 1;
    pointer-events: all;
    color: rgba(255, 255, 255, 0.319);
  }
}
