.hero {
  //   @include center;
  //   display: inline-block;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $text-color-white;
  z-index: 11;
  &__city {
    font-size: 4.8rem;
    font-family: $font-bold;
    opacity: 0.8;
    text-transform: uppercase;
    letter-spacing: 0.3rem;
  }
  &__temp {
    display: flex;
    align-items: center;
    &--sign {
      width: 10rem;
      margin: 1rem;
      text-shadow: 0 0.5rem 2rem black;
    }
    &--value {
      margin: 2rem;
      font-size: 15rem;
      display: inline;
      font-family: $font-thin !important;

      span {
        font-size: 6rem;
        vertical-align: top;
      }
    }
  }
  &__status {
    font-size: 4.8rem;
    margin-bottom: 2rem;
    text-transform: capitalize;
  }
  &__extra {
    text-align: center;
    opacity: 0.7;
    h6 {
      font-size: 2rem;
      margin: 1rem 2rem;
      letter-spacing: 0.2rem;
    }
    &--1 {
      display: flex;
    }
    &--2 {
      display: flex;
      h6 {
        margin: 1rem 4rem;
      }
    }
  }
}
#hero {
  padding: 14rem 0;
}

// details

.detail {
