.favourite__list {
  width: 100%;
  min-height: 100vh;
  position: fixed;
  padding-right: 15px;
  padding-left: 10rem;
  // display: none;
  z-index: 100;
  // opacity: 0;
  // pointer-events: none;
  padding-top: 15rem;
  transition: all 0.4s ease-in-out;
  display: flex;
  flex-wrap: wrap;
  .fav__container {
    width: 10%;
    min-height: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    // justify-content: space-between;
    margin: 0 auto;
    background-color: black;
    padding: 2rem 3rem;
    // padding-left: rem;
    // border-top: 2px solid rgba(255, 255, 255, 0.2);
    // background-color: rgba(0, 0, 0, 0.2);
  }
  .fav__city {
    cursor: pointer;
    font-size: 4rem;
    color: white;
  }
  .fav__img {
    width: 3rem;
    margin-right: auto;
    margin-left: 2rem;
    padding-top: 1rem;
  }
  .fav__del {
    background-color: transparent;
    font-size: 3rem;
    padding: 1rem;
    color: white;
    border: none;
    cursor: pointer;
  }
}
